



































































































import Vue from 'vue'
import { getList, changStatus } from '@/api/course'
import dayjs from 'dayjs'
export default Vue.extend({
  name: 'User',
  data () {
    return {
      dayjs: dayjs,
      loading: false,
      total: 0,
      list: [],
      roleList: [],
      show: false,
      searchParams: {
        courseName: '',
        status: '',
        currentPage: 1,
        pageSize: 10,
      },
      form: {
        roleIdList: [],
        userId: '',
      },
      changeLoading: false,
    }
  },
  created () {
    this.getList(this.searchParams)
  },
  methods: {
    search () {
      this.getList(this.searchParams)
    },
    async getList (searchParams: object) {
      this.loading = true
      try {
        const res = await getList(searchParams)
        this.list = res.data.data.records
        this.total = res.data.data.total
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        this.loading = false
      }
    },
    editCourse (id: number | string) {
      console.log(id)
    },
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    async changStatus (data: any) {
      try {
        this.changeLoading = true
        await changStatus({ courseId: data.id, status: data.status })
        this.changeLoading = false
        this.$message.success(`${data.status ? '' : ''}`)
        // this.getList(this.searchParams)
      } catch (error) {}
    },
    createCourse () {
      this.$router.push({
        name: 'course-create',
      })
    },
  },
})
